


.container{
 position: relative;
}
.container_item{
 margin: 400px auto;
 width: 50%;
 height: 50%;
 text-align: center;

}
.modal__open{
  display: none;
  background-color: rgb(88, 23, 88);
  justify-content: center;
  align-items: center;
  position: absolute;
  border: 3px solid rgb(88, 23, 88);
  z-index: 2;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
#open{
  top: 0;
  width: 50px;
  height: 50px;
  border-radius: 5px;
}

#open:checked~.modal__open{
  display: flex;
}  

#open:hover:after{
  content: attr(data-tips);
  

}